.jg_blocks-inspector_inputs{
	display: flex;
	flex-direction: column;

	.jg_blocks-inspector_input_group{
		margin-bottom: 1rem;
		padding: 0.5rem 0;

		label{
			display: block;
			margin-bottom: 0.5rem;
		}

		input{
			width: 100%;
			padding: 0.5rem;
			border: 1px solid #ccc;
			border-radius: 0.25rem;
		}

	}
}

.jg_blocks-hero_slideshow {
  width: 100%;
  position: relative;
  max-height: 100vh;

  .jg_blocks-hero_slideshow_controls{
	position: absolute;
	top: 50%;
	width: 100%;
	display: flex;
	justify-content: space-between;
	transform: translateY(-50%);

	.jg_blocks-hero_slideshow_control{
		cursor: pointer;
		border: none;
		padding: 0.5rem;
		align-self: center;
		font-size: larger;
	}

	.jg_blocks-hero_slideshow_action_button{
		cursor: pointer;
		border: none;
		padding: 0.5rem 1rem;
		align-self: center;
		text-align: center;
		font-size: larger;
		display: inline-block;
	}

	.jg_blocks-hero_slideshow_title{

	}

	.jg_blocks-hero_slideshow_text{

	}

  }

  .jg_blocks-hero_slideshow_slide{
	
	width: 100%;
	height: 100%;

	&.jg_blocks-hidden{
		display: none;
	}
	.jg_blocks-hero_slideshow_image{
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: center;

		
	}
  }
}
